<template>
<div class="register">
  <div class="container-fluid vf-form-wallpaper">
    <b-row>
      <div class="col-md-6 vf-form ml-auto shadow-lg">
        <p class="h1 text-center m-5">Login</p>
        <b-alert :show="submit" :variant="error.login ? 'success' : 'danger'">
        <p class="h4 alert-heading" v-if="!error.login">Login failed</p>
        <template v-if="!error.login">
            <p v-for="(msg, index) in error.msg" :key="index">
                {{ msg }}
            </p>
        </template>
        </b-alert>
        <b-form @submit.prevent="login">
          <b-form-group label-for="email" label="Email">
            <b-input-group>
              <b-input-group-prepend is-text>
                <b-icon icon="envelope"></b-icon>
              </b-input-group-prepend>
              <b-form-input
                type="email"
                id="email"
                :placeholder="user.email"
                v-model="user.email"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
          
          <b-form-group label-for="password" label="Password">
            <b-input-group>
              <b-input-group-prepend is-text>
                <b-icon icon="person-fill"></b-icon>
              </b-input-group-prepend>
             
              <b-form-input
                type="password"
                id="password"
                :placeholder="user.password"
                v-model="user.password"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
          <b-form-group>
            <b-button type="submit" variant="secondary btn-block" class="mt-3 app-button"
              >Login</b-button
            >
          </b-form-group>
        </b-form>
      </div>
    </b-row>
  </div>
  </div>
</template>

<script>
const api_url = process.env.VUE_APP_API_URL;

export default {
  data() {
    return {
    error: {login: Boolean, msg: ''},
    submit: false,
    user: { email: "", password: "", rememberMe: false },
    };
  },
    methods:{
        login(){
            this.submit = true;
            this.axios.post(`${api_url}/login`, this.user).then((response)=> {
                let token =  response.data.token;
                this.$store.dispatch('login', token).then(() => {
                    this.$router.push({path: '/'});
                }).catch((error)=>{
                    console.log(error);
                })
            }).catch((error) =>{
                this.error.login = false;
                this.error.msg = error.response.data.errors.msg;
                console.log(this.error);
            });
        }
    }
}
</script>

<style scoped>
.container-fluid {
  height: 100vh;
  position: fixed;
}

.vf-form {
  background-color: #3b2e40d3;
  color: white;
  height: 100vh;
}

.vf-form-wallpaper {
  background-image: url("../assets/imgs/vfilm-login-register-wallpaper-rd.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

input:focus {
  outline: #f73859 !important;
  box-shadow: 1px 1px 5px #f73859;
  color: #393e46;
  border-color: #f73859;
}

.input-group svg {
  color: #fff;
}

.h1{
    text-shadow: 1px 2px 4px black;
}

.fade-in {
  animation: fadeIn ease 1s;
}



@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}


@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
}
}
</style>